
.container.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    height: 68vh;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;    
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    /* transform: rotate(8deg); */
    transition: var(--transition);
}

.about__me-image:hover{
    transform: scale(1.1);
    transition: var(--transition);
}
