.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-white);
}

/* media queries - medium devices */
@media screen and  (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
        height: auto;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        font-size: large;
        margin: 1rem 0 1.5rem;
    }
    
}
/* media queries - small devices */
@media screen and  (max-width: 600px) {
    
    
    .container.about__container{
        grid-template-columns: 1fr;
        gap: 0;
        height: auto;
        /* overflow: scroll; */
    }
    .about__me{
        width: 60%;
        margin: 0 auto 3rem;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        /* padding-top: 0rem; */
        font-size: small;
        text-align: justify;
        overflow: scroll;
    }

    .about__content p {
        font-size: small;
        margin: 1.5rem 0;
    }
    
}